import React, { Fragment} from "react"
import {NavLink} from "react-router-dom"


import "./Nav.css";


class Nav extends React.Component{
   

   
    
  render(){

   
   return (
        
        <Fragment>
    
         
        <td 
        onClick={()=> this.props.HC()} 
        className="NavTableTD"> 
        <button 
        style={this.props.Appstyle} 
        className="NavBtn">  
        <NavLink 
        className="NavTabeleTD" 
        activeClassName="clicked" 
        to="/Startsite">
        Home Page 
        </NavLink></button>
        </td> 

        <td 
        id="ContentNav"
        onClick={()=> this.props.HC()} 
        className="NavTableTD"> 
        
        <button 
        style={this.props.Appstyle} 
        className="NavBtn">  
        
        <NavLink 
        className="NavTabeleTD"  
        activeClassName="clicked" 
        to="/Hero">
        Content 
        <div id="OptionstoDropDown">
          Hero/Maps
        </div>
        </NavLink> 
        </button> 
        
        <div className="drop-down-content">
        
        <button 
        style={this.props.Appstyle} 
        className="NavBtn">  
        <NavLink 
        className="NavTabeleTD"  
        activeClassName="clicked"  
        to="/Maps">Maps 
        </NavLink>
        </button>
        
        <button 
        style={this.props.Appstyle} 
        className="NavBtn">   
        <NavLink className="NavTabeleTD"  
        activeClassName="clicked"  
        to="/Hero">Hero
        </NavLink>
        </button>
        </div>
        </td>



        <td 
        onClick={()=> this.props.HC()} 
        className="NavTableTD">
        <button style={this.props.Appstyle} 
        className="NavBtn">
        <NavLink  
        className="NavTabeleTD"
        activeClassName="clicked" 
        to="/News"> 
        News
       </NavLink>
       </button>
        </td>
        
        <td 
        onClick={()=> this.props.HC()} 
        className="NavTableTD"> 
        <button 
        style={this.props.Appstyle} 
        className="NavBtn">
        <NavLink 
        className="NavTabeleTD" 
        activeClassName="clicked" 
        to="/DLC"> 
        DLC
        </NavLink>
        </button>
        </td>
        
        <td 
        onClick={()=> this.props.HC()} 
        className="NavTableTD"> 
        
        <button 
        style={this.props.Appstyle} 
        className="NavBtn">
        
        <NavLink 
        className="NavTabeleTD" 
        activeClassName="clicked" 
        to="/Sign up">
        Sign up
        </NavLink></button>
        </td>
        </Fragment>
           
         

        
        
    )} 
   
}
// <NavLink activeClassName="clicked" to="/Hero"> Hero</NavLink>
// <NavLink activeClassName="clicked" to="/News">News</NavLink>
// <NavLink activeClassName="clicked" to="/OurTeam">OurTeam</NavLink>
// <NavLink activeClassName="clicked" to="/Startsite">Contact</NavLink>
export default Nav