import React from "react"
import ParallaxCard from 'react-parallax-card'
import MapHarbor from "./img/Harbor.jpg"
import "./Maps.css"
const Maps = () =>{
    return (
      <div className="MapsClass">
      <h1>You can have a deeper look at the Maps now!</h1>
        <ParallaxCard
        label='Harbor Map'
        enableRotate
        enableTransform
        style={{ margin: '0 auto', width: 500, height: 240 }}>
      
        <img src={MapHarbor} style={{ maxWidth: '500px', maxHeight: '100%' }} />
      
        <div /><div /><div />
      
        <img  style={{ top: '67%', left: '75%'  }} />
      
        <p style={{ fontSize: '3rem', fontWeight: 'bold', padding: '3rem' }}>
          
        </p>
      
      </ParallaxCard>
      </div>
    )

}
export default Maps